import React from 'react';
import Center from 'react-center';

import './Styles/skills.css';

const Skills = (props) => {
    return (
        <div className='skills-container-box'>
            <div className='js-box'>
            <Center>
                JavaScript - ES5/6 ReactJS Router Redux jQuery NodeJS
            </Center>
            </div>
            <div className='gap-div'></div>
            <div className='python-box'>
            <Center>
                Python - Django Tkinter PyQt OpenCV NumPy SciPy
            </Center>
            </div>
            <div className='gap-div'></div>
            <div className='sql-box'>
            <Center>
                SQL - MySQL PostgreSQL SQLite
            </Center>
            </div>
            <div className='gap-div'></div>
            <div className='also-box'>
            <Center>
                Also, C/C++ Golang Solidity MATLAB Bash
            </Center>
            </div>
        </div>
    );
}

export default Skills;


