import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

import { SocialIcon } from 'react-social-icons';
import Description from '@material-ui/icons/Description';

const myResume = 'https://drive.google.com/file/d/1V8Qy6jP9xXRtMzEcDPepp7qZdlrI-4vb/view?usp=sharing';

const styles = {
  root: {
    flexGrow: 1,
  },
  flex: {
    flex: 1,
  }
};

function ButtonAppBar(props) {
  const { classes } = props;
  return (
    <div className={classes.root}>
      <AppBar style={{ background: 'transparent', verticalAlign: 'top', boxShadow: 'none'}}>
        <Toolbar>
          <Typography variant="title" className={classes.flex}>
          </Typography>
          <Button><SocialIcon url="https://www.linkedin.com/in/sridhar-pandian-20aa13137/" color="#24292e" /></Button>
          <Button><SocialIcon url="https://github.com/SridharPandian" color="#24292e" /></Button>
          <Button><a href={myResume} style={{color: "#24292e"}}><Description className={classes.icon} style={{ fontSize: 50 }} /></a></Button>

        </Toolbar>
      </AppBar>
    </div>
  );
}

ButtonAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ButtonAppBar);


