import React from 'react';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SkillsIcon from '@material-ui/icons/Settings';
import PhoneIcon from '@material-ui/icons/Phone';
import BuildIcon from '@material-ui/icons/Build';
import AssignmentIcon from '@material-ui/icons/Assignment';
import PersonPinIcon from '@material-ui/icons/PersonPin';

const bottomAlign = {
    bottom: 0,
    position: 'absolute',
    width: '100%',
    verticalAlign: 'bottom'
}

export default class BottomTab extends React.Component {
  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };

  render() {
    return (
      <Paper style={bottomAlign}>
        <Tabs
          value={this.state.value}
          onChange={this.handleChange}
          fullWidth
          centered
          indicatorColor="inherit"
          textColor="inherit"
        >
          <Tab icon={<PersonPinIcon />} label="Name" onClick={this.props.changeName} />
          <Tab icon={<AssignmentIcon />} label="About" onClick={this.props.changeAbout} />
          <Tab icon={<BuildIcon />} label="Interests" onClick={this.props.changeInterests} />
          <Tab icon={<SkillsIcon />} label="Skills" onClick={this.props.changeSkills}/>
          <Tab icon={<PhoneIcon />} label="Contact" onClick={this.props.changeContact} />
        </Tabs>
      </Paper>
    );
  }
}


