import React from 'react';
import Center from 'react-center';

import './Name.css';

const Name = (props) => {
    return (
        <div className='name-container-box'>
            <div className='greeting-box'>
            <Center>
                Hello! Vanakam! Namasté! Bonjour!
            </Center>
            </div>
            <div className='gap-div'></div>

            <div className='myname-box'>
            <Center>
                I'm Sridhar Pandian
            </Center>
            </div>
            <div className='gap-div'></div>

            <div className='undergrad-box'>
            <Center>
                An undergraduate student           
            </Center>
            </div>
        </div>
    );
}

export default Name;
