import React from 'react';
import BottomTab from './BottomTab';
import TopTab from './TopTab';
import Name from './NameComponents/Name';
import About from './About';
import Interests from './Interests';
import Contact from './Contact';
import Skills from './Skills';

class Application extends React.Component {
    state = {
        dispTab: 0
    };

    changeName = (e) => {
        this.setState({ dispTab: 0 });
    };

    changeAbout = (e) => {
        this.setState({ dispTab: 1 });
    };

    changeInterests = (e) => {
        this.setState({ dispTab: 2 });
    };

    changeSkills = (e) => {
        this.setState({ dispTab: 3 });
    };

    changeContact = (e) => {
        this.setState({ dispTab: 4 });
    };

    render() {
        return (
            <div>
                <TopTab />
                {this.state.dispTab===0 && <Name />}
                {this.state.dispTab===1 && <About />}
                {this.state.dispTab===2 && <Interests />}
                {this.state.dispTab===3 && <Skills />}
                {this.state.dispTab===4 && <Contact />}
                <BottomTab 
                    changeName = {this.changeName}
                    changeAbout = {this.changeAbout}
                    changeInterests = {this.changeInterests}
                    changeSkills = {this.changeSkills}
                    changeContact = {this.changeContact}
                />
            </div>
        );
    }
}

export default Application;


