import React from 'react';
import Center from 'react-center';

import './Styles/about.css';

const About = (props) => {
    return (
        <div className='name-box'>
        <div className='chennai-box'>
            <Center>
            A typical Chennaite
            </Center>
        </div>
        {/* <div className='gap-div'></div> */}
        <div className='degree-box'>
            <Center>
            An Electronics and Communication Undergrad
            </Center>
        </div>
        <div className='gap-div'></div>
        <div className='college-box'>
            <Center>
                Currently studying in Vellore Institute of Technology, Chennai Campus
            </Center>
        </div>
        </div>
    );
}

export default About;


